import React from 'react'
import Helmet from 'react-helmet'
import ContactForm from '../partials/ContactForm'
import FooterInfo from '../partials/FooterInfo'
import SocialLinks from '../partials/SocialLinks'
import ContactLinks from '../partials/ContactLinks'

const ContactPage = () => (
  <div>
    <div className="contact-page align-center">
      <Helmet
        title="Contact Us | Fitato"
      >
        <meta name="description" content="Fitato Team is always there to help you. Just share your details and one of our team will contact you as soon as possible." />
      </Helmet>
      <div className="container">
        <h1>Get in Touch - Available 24*7</h1>
        <div className="contact-page-form">
          <p>We are always available at your service, if you have any further queries/questions or even need a workout partner, connect with us.</p>
          <ContactForm />
        </div>
        <ContactLinks cname="footer-ul" />
        <SocialLinks cname="footer-social" />
      </div>
    </div>
    <FooterInfo />
  </div>
)

export default ContactPage



